import React from 'react'

function Competitive(props) {
    const CardImageUrl = "https://images.unsplash.com/photo-1566241477600-ac026ad43874?ixid=MnwxMjA3fDB8MHxzZWFyY2h8Mnx8d2VicGFnZXxlbnwwfHwwfHw%3D&ixlib=rb-1.2.1&w=1000&q=80"
    const CardFlexBox = "d-flex justify-content-center mt-5";
    const Links = {
        Binary:"https://binarysearch.com/@/ujjawalrachhoya",
        Codechef: "https://www.codechef.com/users/ujjawal100"
    }
    return (
        <>
            <div className="container mt-5">
                <h3 className="text-center mt-4" style={props.ProjectStyle}>Competitive Programming</h3>
                <div className="row mb-5 d-flex justify-content-center">
                    <div className={`col-md-4 ${CardFlexBox}`}>
                        <div class="card" style={props.CardStyle}>
                            <img style={props.ImgAnimation} src={props.Image1} class="card-img-top" alt="..." height="150" />
                            <div class="card-body">
                                <h5 class="card-title">Binary search</h5>
                                <p class="card-text">1700+ experience, 63 +level and more than 240 question solved</p>
                                <a style={{ boxShadow: "none" }} rel="noopener" href={Links.Binary} target="_blank" class="btn btn-dark">Binary Search</a>
                            </div>
                        </div>
                    </div>
                    <div className={`col-md-4 ${CardFlexBox}`}>
                        <div class="card" style={props.CardStyle}>
                            <img style={props.ImgAnimation} height="150" src={props.Image2} class="card-img-top" alt="..." />
                            <div class="card-body">
                                <h5 class="card-title">Codechef</h5>
                                <p class="card-text">3 star coder and more than 120 question solved</p>
                                <a style={{ boxShadow: "none" }} rel="noopener" href={Links.Codechef} target="_blank" class="btn btn-dark">Codechef</a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default Competitive
