import './App.css';
import Navbar from './components/Navbar';
import Body from './components/Body'
import { useState } from "react"
import Project from './components/Project';
import About from './components/About';
import Footer from './components/Footer';
import Competitive from './components/Competitive';
function App() {
  const [light, Setlight] = useState('dark')
  const [bglight, Setbglight] = useState('light')
  const [modeName, SetmodeName] = useState('Enable Dark Mode')
  // document.body.style.backgroundColor = "#edf1f3"
  const togglemode = () => {
    console.log("This is toggle mode")
    if (light === 'dark') {
      Setlight('light');
      document.body.style.backgroundColor = "#202529";
      Setbglight('dark')
      SetmodeName('Enable Light Mode')
      SetTextColor({
        color: "white"
      })
      SetCardStyle({
        backgroundImage: "linear-gradient(to bottom right,#15171f,#a6a6a8)",
        width: "18rem",
        color: "white"
      })
      SetProjectStyle({
        color: "white"
      })
    }
    else {
      Setlight("dark");
      document.body.style.backgroundColor = "#edf1f3";
      Setbglight('light')
      SetmodeName('Enable Dark Mode')
      SetTextColor({
        color: "black"
      })
      SetCardStyle({
        // backgroundColor:"#fdfdff",
        backgroundImage: "linear-gradient(to bottom right,#ecf1f2, rgba(0,0,0,0.1))",
        width: "18rem",
        color: "black"
      })
      SetProjectStyle({
        color: "black"
      })
    }
  }

  const [TextColor, SetTextColor] = useState({
    color: "black"
  })
  const [NameStyle, SetNameStyle] = useState({
    color: "#fc004e",
    fontWeight: "700"
  })
  const [MyWorld, SetMyWorld] = useState({
    color: "#696b6f"
  })
  const [CardStyle, SetCardStyle] = useState({
    width: "18rem",
    backgroundImage: "linear-gradient(to bottom right,#ecf1f2, rgba(0,0,0,0.05))",
    color: "black"
  })
  const [ProjectStyle, SetProjectStyle] = useState({
    color: "black",
  })
  return (
    <>
      <Navbar togglemode={togglemode} bglight={bglight} light={light} modeName={modeName} />
      <Body  MyWorld={MyWorld} NameStyle={NameStyle} TextColor={TextColor} />
      <About TextColor = {TextColor} MyImage = "./MyImage.jpg"/>
      <Project  Image7= "./Portfolio.png" Image4= './Brave.png' Image5="./Covid.png" Image6="Cal.png"  Image1='./FaceBook.png' Image2='./YouTube.png' Image3='./InstaGram.png' ProjectStyle={ProjectStyle}  CardStyle={CardStyle} />
      <Competitive ProjectStyle={ProjectStyle} Image1="./Binary.png" Image2="./Codechef.png" CardStyle={CardStyle} />
      <Footer color = {TextColor}/>
    </>
  );
}

export default App;
