import React from 'react'

function Footer(props) {
    return (
        <div className="container-fluid d-flex flex-column justify-content-center">
            <h3 className="text-center mb-3" style={props.color}>Social Media</h3>
            <ul className="d-flex justify-content-center" style={{listStyle:"none"}}>
                <li><a class="btn text-light mx-2" style={{backgroundColor:"#333333"}} target="_blank" href="https://github.com/noobtuber20103152" role="button"><i class="fab fa-github"></i></a></li>
                <li><a class="btn text-light mx-2 " style={{backgroundColor:"#ed302f"}} target="_blank" href="https://www.youtube.com/c/LegendXpro100" role="button"><i class="fab fa-youtube"></i></a></li>
                <li><a class="btn text-light mx-2" style={{backgroundColor:"#fccc63"}} target="_blank" href="https://www.instagram.com/fetch.arrow.fun/" role="button"><i class="fab fa-instagram"></i></a></li>
            </ul>
            <p className="text-center" style={props.color}>C | C++ | HTML | CSS | JavsScript | React.js | PHP | DSA | Video Editing | YouTube</p>
        </div>
    )
}
export default Footer
