import React, { Component, useState } from 'react'
// import PropTypes from 'prop-types'
export default function Navbar(props) {
    const [color, setcolor] = useState({
        backgroundColor: "#202529",
        color: "white"
    })
    return (
        <>
            <nav class={`navbar navbar-expand-lg navbar-light bg-${props.bglight} sticky-top`} style={color}>
                <div class="container-fluid">
                    <a class="navbar-brand " href="/" style={{ color: "#fe014f" }}>Portfolio</a>
                    <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation" style={{ color: "white", boxShadow: "none" }}>
                        <span class="navbar-toggler-icon text-light" ></span>
                    </button>
                    <div class="collapse navbar-collapse" id="navbarNav">
                        <div>
                            <div class="form-check form-switch d-flex align-items-center">
                                <input class="form-check-input" type="checkbox" role="switch" onClick={props.togglemode} id="flexSwitchCheckDefault" style = {{boxShadow:"none"}} />
                                {/* <label class={`form-check-label text-${props.light}`} for="flexSwitchCheckDefault"  >{props.modeName}</label> */}
                            </div>
                        </div>
                    </div>

                </div>
            </nav>
        </>
    )
}