import React, { Component, useState } from 'react'
// import Typewriter from "typewriter-effect";
import Typist from "react-typist";

// import PropTypes from 'prop-types'
export default function Navbar(props) {
    const [count, setCount] = useState(1);
    return (
        <>
            <div className="container  d-flex justify-content-center flex-column" style = {{height:"40vh" , bottom:"0"}}>
                <div className="container px-0">
                    <h6 className="mt-3 text-center" style={props.MyWorld}>Welcome to my world</h6>
                </div>
                <div className="container mt-3 px-0">
                    <h1 className='text-center' style={props.TextColor}>Hi, I'm <span style={props.NameStyle}>
                            <Typist avgTypingDelay={100} onTypingDone={() => setCount(0)}>
                                <span> Web developer</span>
                                <Typist.Backspace count={20} delay={800} />
                                <span> Ujjawal Rachhoya</span>
                            </Typist>
                       </span></h1>
                </div>
                <div className="container px-0 ">
                    <h2 className='text-center' style={props.TextColor}>a Web developer</h2>
                </div>
            </div>
        </>
    )
}