import React  from 'react'

function About(props) {
    return (
        <div className="container d-flex justify-content-center">
            <div className="row container d-flex justify-content-center">
            <div className="col-md-3">
                <div className="container border d-flex justify-content-center">
                    <img id="myimg" src= {props.MyImage} width= "285px" alt="loading"></img>
                </div>
            </div>
            <div className="col-md-7 mt-5">
                <div className="container border h-100 d-flex align-items-center flex-column justify-content-center">
                    <h6 className="text-center mt-4" style = {props.TextColor}>Who I <span style= {{color:"red"}}>am</span> ?</h6>
                    <p style = {props.TextColor}>I am a website designer and developer from Jhunjhunu, Rajasthan, India with a strong focus in web development and competitive coding. I love to get new experience and always learn from my surrouding</p>
                    <p style={props.TextColor}>Currently, I am pursuing b.tech study in computer science and engneering at Dr. B. R . Ambedkar National Institute Of Technology, Jalandhar. I am a 2nd year student</p>
                </div>
            </div>
        </div>
        </div>
    )
}

export default About
